.item div {
  position: relative;
}

.item p {
  text-transform: capitalize;
  color: #424242;
}

.item.itemSelection p {
  color: #14b3f0;
}

.item:hover p {
  color: #14b3f0 !important;
}

.ItemSelectionContainer::-webkit-scrollbar {
  width: 7px;
}

.placeHoldCont {
  margin-top: 25px;
}

.ILtuvieja {
  height: 210px !important;
}

.ILplaceholder {
  margin: 6px 0 !important;
  font-size: 12px;
  color: #808080a8;
}

.ItemSelectionContainer::-webkit-scrollbar-thumb {
  background: #475a681f;
}

.ItemSelectionContainers {
  display: grid;
  justify-items: center;
  grid-template-rows: 110px 1fr;
}

.containerItems {
  display: grid;
  height: auto;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2 1fr);
  padding: 15px;
  grid-gap: 20px;
  justify-items: center;
  /*align-items: center;*/
}

.item:hover {
  -webkit-transform: scale(1.1) !important;
          transform: scale(1.1) !important;
  box-shadow: 0px 0px 20px -1px rgba(0, 0, 0, 0.6);
  background: -webkit-gradient(linear, right top, left bottom, from(#2b3742), to(#060809));
  background: linear-gradient(to bottom left, #2b3742, #060809);
  color: #1ca8eb;
  border: 1px solid #455869;
}

.itemSelection {
  -webkit-transform: scale(1.1) !important;
          transform: scale(1.1) !important;
  box-shadow: 0px 0px 20px -1px rgba(0, 0, 0, 0.6);
  background: -webkit-gradient(linear, right top, left bottom, from(#2b3742), to(#060809));
  background: linear-gradient(to bottom left, #2b3742, #060809);
  color: #1ca8eb;
  border: 1px solid #455869;
}

.item {
  -webkit-transform: scale(1);
          transform: scale(1);
  border: 1px solid #22293333;
  border-radius: 7px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  height: 140px;
  cursor: pointer;
  display: grid;
  align-items: center;
  justify-items: center;
  box-shadow: 0px 0px 20px -1px rgba(0, 0, 0, 0);
  grid-template-rows: 1fr 36px;
  width: 170px;
  margin-bottom: 7px;
}

.item:hover .banquis {
  opacity: 1;
}
.banquis {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  opacity: 0.3;
}

.itemVisible {
  opacity: 1 !important;
}

.banquis:hover {
  opacity: 1;
}

.itemSobre {
  display: none !important;
}

.itemSobre.activaos {
  display: inline !important;
  display: initial !important;
}

.itemFuera {
  display: inline;
  display: initial;
}

.fuerActive {
  display: none;
}

.item:hover .itemSobre {
  display: inline !important;
  display: initial !important;
}

.item:hover .itemFuera {
  display: none;
}

.item p {
  margin: 0 0 14px 0 !important;
}

/*.AccountItemList:hover .bigEgg{
  animation-name: bigEgg;
}*/

.bigEgg {
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  /*animation-delay: .17s;*/
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes bigEgg {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bigEgg {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.egg {
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes egg {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  60% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes egg {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  60% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes egg2 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes egg2 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.active_account {
  display: grid;
  align-items: center;
  justify-items: center;
  width: 25px;
  height: 25px;
  position: relative;
  align-self: center;
  justify-self: center;
  -webkit-transform: scale(0.8);
